<template>
  <div class='outer'>
    <div>
      <div class="title name">
        <span>{{info.stockCode}}</span><span>{{info.stockName}}</span>
      </div>
      <div class="title">
        <span>{{news.price}}</span><span>{{news.range}}
        </span><span>{{news.percentage}}%</span>
      </div>
    </div>
    <div class="content1">
      <ul>
        <li v-for="(d,i) in sellDetails" v-if="i!=9">
          <span>卖{{chineseNumber[i]}}</span>
          <span>{{d.price}}</span>
          <span>{{d.num}}</span>
        </li>
      </ul>
      <div class="left">
        <div class="header">
          <div><span>委比 {{statistics.committee}}</span><span>委差 {{statistics.commission}}</span></div>
          <div><span>均卖 {{statistics.avgSell}}</span><span>总卖 {{statistics.totalSell}}</span></div>
        </div>
        <div>
          <div class="one" v-for="d in sellOneDetails">{{d}}</div>
        </div>
      </div>
    </div>
    <div class="sell-one">
      <div class="sell-one-old">
        <span>卖{{chineseNumber[9]}}</span>
        <span>{{sellDetails[9].price}}</span>
        <span>{{sellDetails[9].num}}</span>
      </div>
      <div class="sell-one-new"><span>{{sell.num}}笔 </span><span>↑</span></div>
    </div>
    <div class="buy-one">
      <div class="buy-one-old">
        <span>买{{chineseNumber[9]}}</span>
        <span>{{buyDetails[0].price}}</span>
        <span>{{buyDetails[0].num}}</span>
      </div>
      <div class="buy-one-new"><span>{{buy.num}}笔 </span><span>↓</span></div>

    </div>
    <div class="content2">
      <ul>
        <li v-for="(d,i) in buyDetails" v-if="i!=0">
          <span>买{{chineseNumber[9-i]}}</span>
          <span>{{d.price}}</span>
          <span>{{d.num}}</span>
        </li>
      </ul>

      <div class="left">
        <div>
          <div class="one" v-for="d in buyOneDetails">{{d}}</div>
        </div>
        <div class="footer">
          <div><span>均买 {{statistics.avgBuy}}</span><span>总买 {{statistics.totalBuy}}</span></div>
          <div><span>总笔 {{statistics.totalNum}}</span><span>均笔 {{statistics.avgNum}}</span></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'TransactionDetails',
    props: {
      info: Object,
      sellDetails: Array,
      buyDetails: Array,
      news: Object,
      statistics: Object,
      sellOneDetails: Array,
      buyOneDetails: Array,
      sell: Object,
      buy: Object
    },
    data() {
      return {
        chineseNumber: ['十', '九', '八', '七', '六', '五', '四', '三', '二', '一']
      }
    }
  }
</script>
<style scoped>
  * {
    padding: 0;
    margin: 0;
    border: none;
    box-sizing: border-box;
  }

  .outer {
    min-width: 300px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #313335;
    color: #a7a2a2;
    border: 1px solid #ddd;
    font-size: 12px;
  }

  ul {
    list-style: none;
    flex: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-left: 3px;
    padding-right: 3px;
    border-right: 1px solid #ddd;
  }

  ul li {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .title {
    height: 20px;
    border-bottom: 1px solid #ddd;
    width: 50%;
    float: left;
    display: flex;
    justify-content: space-around;
  }
 .name{
   border-right: 1px solid #ddd;
 }
  .sell-one {
    border-bottom: 1px dashed #ddd;
  }

  .content1, .content2 {
    flex: 9 1 0%;
    display: flex;
    align-items: stretch;

  }

  .content1 > div, .content2 > div {
    width: 50%;
    float: left;
  }

  .header{
    border-bottom: 1px solid #ddd;
  }
  .footer{
    border-top: 1px solid #ddd;
  }
  .header, .footer {
    height: 30px;
  }

  .header div, .footer div {
    line-height: 15px;
    display: flex;
    justify-content: space-between;
    padding: 0 3px;
  }

  .one {
    height: 20px;
    font-size: 13px;
    min-width: 30px;
    width: 20%;
    line-height: 20px;
    display: inline-block;
    text-align: center;
  }

  .left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .sell-one,.buy-one{
    flex: 1 1 0%;
    display: flex;
    align-items: stretch;
  }
  .sell-one{
    border-top: 1px solid #ddd;
  }
  .buy-one{
    border-bottom: 1px solid #ddd;
  }

  .buy-one > div, .sell-one > div {
    width: 50%;
    float: left;
    padding: 0 3px;
  }

  .buy-one-old, .sell-one-old {
    display: flex;
    justify-content: space-between;
    border-right: 1px solid #ddd;
    align-items: center;
  }

  .buy-one-new, .sell-one-new {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
</style>



