<template>
  <div class='kline'></div>
</template>

<script>
  import $ from 'jquery'
  import _ from 'lodash'
  import * as d3 from 'd3'

  // http://blog.scottlogic.com/2014/09/26/an-interactive-stock-comparison-chart-with-d3.html

  window.d3 = d3

  let originData = require('./data.csv')
  let nowData = []
  const techan = require('techan')

  const margin = {
    top: 50,
    right: 50,
    bottom: 50,
    left: 50,
    volumeHeight: 150
  }

  const parseDate = d3.timeParse('%d-%b-%y')
  const bisectDate = d3.bisector(d => d.date).left

  // K线
  let svg
  let x
  let y
  let candlestick
  let accessor
  let xAxis
  let yAxis

  // 成交量
  let volume
  let volumeY
  let volumeAccessor
  let volumeYAxis

  // 十字架
  let crosshair

  // 注解
  let timeAnnotation
  let ohlcAnnotation
  let volumeAnnotation

  // 展示信息
  let coordsText
  let volumeText

  // 均值
  let ma5
  let ma10
  let ma20

  // 涨跌
  let yPercent
  let percentAxis
  let percentAnnotation

  /**
   * 处理数据
   * @param data
   * @param count
   */
  function parseData(data, count) {
    count = parseInt(count)
    let begin = data.length - count
    begin = begin > -1 ? begin : 0

    return data.map(d => {
      return {
        date: parseDate(d.Date),
        open: +d.Open,
        high: +d.High,
        low: +d.Low,
        close: +d.Close,
        volume: +d.Volume
      }
    }).sort((a, b) => d3.ascending(accessor.d(a), accessor.d(b)))
      .slice(begin, data.length)
  }

  /**
   * 第一次初始化SVG
   * @param el
   * @param width
   * @param height
   */
  function initSVG(el, width, height) {
    initDomain(width, height)

    let viewHeight = height - margin.top - margin.bottom
    let viewWidth = width - margin.left - margin.right

    svg = d3.select(el)
      .append('svg')
      .append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`)

    const candlestickSelection = svg.append('g').attr('class', 'candlestick')
    candlestickSelection.append('g').attr('class', 'x axis axis-candlestick').attr('transform', `translate(0, ${viewHeight})`)
    candlestickSelection.append('g').attr('class', 'y axis axis-candlestick')

    svg.append('g').attr('class', 'volume')
    svg.append('g').attr('class', 'y axis axis-volume')

    // 均值
    candlestickSelection.append('g').attr('class', 'indicator sma ma5')
    candlestickSelection.append('g').attr('class', 'indicator sma ma10')
    candlestickSelection.append('g').attr('class', 'indicator sma ma20')

    // 涨跌幅度
    candlestickSelection.append('g').attr('class', 'percent axis').attr('transform', `translate(${viewWidth}, 0)`)

    // 十字架
    svg.append('g').attr('class', 'crosshair')

    coordsText = svg.append('text')
      .attr('class', 'coords').attr('y', -10)

    volumeText = svg.append('text')
      .attr('class', 'corrds').attr('y', `${viewHeight - margin.volumeHeight + 16}`)
  }

  /**
   * 初始化Domain, 当画布大小改变后需要重新设置Domain
   * @param width
   * @param height
   */
  function initDomain(width, height) {
    width = width - margin.left - margin.right
    height = height - margin.top - margin.bottom

    x = techan.scale.financetime().range([0, width])
    y = d3.scaleLinear().range([height - 150, 0])
    candlestick = techan.plot.candlestick().xScale(x).yScale(y)
    accessor = candlestick.accessor()
    xAxis = d3.axisBottom().scale(x).tickFormat(d3.timeFormat('%Y-%m'))
    yAxis = d3.axisLeft().scale(y)

    // 成交量
    volumeY = d3.scaleLinear().range([height, height - margin.volumeHeight + 20])
    volume = techan.plot.volume().accessor(techan.accessor.ohlc()).xScale(x).yScale(volumeY)
    volumeAccessor = volume.accessor()
    volumeYAxis = d3.axisLeft(volumeY).ticks(3).tickFormat(d3.format(',.3s'))

    // 注解
    timeAnnotation = techan.plot.axisannotation().axis(xAxis).orient('bottom')
      .width(80).format(d3.timeFormat('%Y-%m-%d')).translate([0, height])
    ohlcAnnotation = techan.plot.axisannotation().axis(yAxis).orient('left')
      .format(d3.format(',.2f'))
    volumeAnnotation = techan.plot.axisannotation().axis(volumeYAxis).orient('left')
      .format(d3.format(',.3s'))

    // 均值
    ma5 = techan.plot.sma().xScale(x).yScale(y)
    ma10 = techan.plot.sma().xScale(x).yScale(y)
    ma20 = techan.plot.sma().xScale(x).yScale(y)

    // 涨跌
    yPercent = y.copy()
    percentAxis = d3.axisRight(yPercent).tickFormat(d3.format('+.1%'))
    percentAnnotation = techan.plot.axisannotation().axis(percentAxis).orient('right').translate([x(1), 0])

    // 十字架
    crosshair = techan.plot.crosshair().xScale(x).yScale(d3.scaleLinear().range([height, 0]))
      .xAnnotation([timeAnnotation])
      .yAnnotation([ohlcAnnotation, volumeAnnotation, percentAnnotation])
      .on('enter', () => {
        coordsText.style('display', 'inline')
        volumeText.style('display', 'inline')
      })
      .on('out', () => {
        coordsText.style('display', 'none')
        volumeText.style('display', 'none')
      })
      .on('move', function(coords) {
        let y0 = d3.mouse(this)[1]
        let i = bisectDate(nowData, coords.x, 1)
        let d = nowData[i]

        coordsText.text(`
          万科A
          日期: ${timeAnnotation.format()(coords.x)}
          价格: ${ohlcAnnotation.format()(y.invert(y0))}
          涨跌幅: ${d3.format('+.1%')(yPercent.invert(y0))}
          均价: ${ohlcAnnotation.format()((d.open + d.close) / 2)}
          现手 2759
          房地产
         `)

        volumeText.text(`
          VOL:
          ${d3.format(',.8r')(d.volume)}
          成交量数值2
          成交量数值3
        `)
      })
  }

  /**
   * 具体绘制K线
   * @param data
   */
  function draw(data) {
    nowData = data
    x.domain(data.map(accessor.d))
    y.domain(techan.scale.plot.ohlc(data, accessor).domain())
    svg.selectAll('g.candlestick').datum(data).call(candlestick)
    svg.selectAll('g.x.axis.axis-candlestick').call(xAxis)
    svg.selectAll('g.y.axis.axis-candlestick').call(yAxis)

    volumeY.domain(techan.scale.plot.volume(data, volumeAccessor.v).domain())
    svg.selectAll('g.volume').datum(data).call(volume)
    svg.selectAll('g.y.axis.axis-volume').datum(data).call(volumeYAxis)

    // 十字架
    svg.selectAll('g.crosshair').call(crosshair)

    // 均值
    svg.selectAll('g.sma.ma5').datum(techan.indicator.sma().period(5)(data)).call(ma5)
    svg.selectAll('g.sma.ma10').datum(techan.indicator.sma().period(10)(data)).call(ma10)
    svg.selectAll('g.sma.ma20').datum(techan.indicator.sma().period(20)(data)).call(ma20)

    yPercent.domain(techan.scale.plot.percent(y, accessor(data[data.length - 1])).domain())
    svg.selectAll('g.percent.axis').call(percentAxis)
  }

  export default {
    name: 'KLine',

    data() {
      return {
        width: 0,
        height: 0
      }
    },

    mounted() {
      const vm = this
      const el = $(this.$el)

      this.width = el.width()
      this.height = el.height()

      initSVG(this.$el, this.width, this.height)
      draw(parseData(originData, this.width / 8))

      $(window).on('resize', _.debounce(() => {
        let width = el.width()
        let height = el.height()

        if (!width || !height) {
          return
        }

        vm.width = width
        vm.height = height

        initDomain(width, height)
        draw(parseData(originData, width / 8))
      }, 100))
    }
  }
</script>

<style lang='less'>
  .kline {
    display: flex;
    flex: auto;
    background: #313335;
    font: 12px sans-serif;

    .crosshair {
      cursor: crosshair;

      path.wire {
        stroke: #a7a2a2;
        stroke-dasharray: 1, 1;
      }

      .axisannotation {
        path {
          fill: #222;
        }
      }
    }

    svg {
      flex: auto;
    }

    text {
      fill: #a7a2a2;
    }

    .axis {
      path, line {
        fill: none;
        stroke: #a7a2a2;
        shape-rendering: crispEdges;
      }
    }

    .ma5 path.line {
      stroke: #1f77b4;
    }

    .ma10 path.line {
      stroke: #aec7e8;
    }

    .ma20 path.line {
      stroke: #ff7f0e;
    }

    path {
      fill: none;
      stroke-width: 1;

      /* 成交量 */
      &.volume {
        fill: #ddd;
        opacity: .1;
      }

      /* K线 */
      &.candle {
        stroke: #a7a2a2;

        &.body {
          stroke-width: 0;
        }

        &.up {
          fill: #0a0;
          stroke: #0a0;
        }

        &.down {
          fill: #f00;
          stroke: #f00;
        }
      }
    }
  }
</style>



