<template>
  <div class="tabel-change">
    <ul class="top">
      <li v-for="(d,i) in tabelArray" @click="exChange(i)" :class="selectIndex===i ? 'active':''">{{d}}</li>
    </ul>
    <div class="content">
      <EntrustingaDeal v-show="selectIndex===0" :infoweituo="infoweituo"></EntrustingaDeal>
      <DeputeTransactionDetails  v-show="selectIndex===1" :infoweituo="infoweituo" :result="result"></DeputeTransactionDetails>
      <TransactionReduction v-show="selectIndex===2"></TransactionReduction>
    </div>
  </div>
</template>

<script>
  import EntrustingaDeal from './EntrustingaDeal'
  import DeputeTransactionDetails from './DeputeTransactionDetails'
  import TransactionReduction from './TransactionReduction'
  export default {
    name: 'TabelChange',

    data() {
      return {
        selectIndex: 0,
        tabelArray: ['逐笔成交明细', '逐笔成交还原', '委托成交流水']
      }
    },

    props: {
      infoweituo: Array,
      result: Array
    },

    components: {
      EntrustingaDeal,
      DeputeTransactionDetails,
      TransactionReduction
    },

    methods: {
      exChange(index) {
        this.selectIndex = index
      }
    }
  }
</script>

<style scoped lang="less">
  .tabel-change {
    height: 100%;
    background: #313335;
    border:1px solid #ddd;
    border-top:none;
    .top {
      display: flex;
      background: #191919;
      li{
        list-style: none;
        color:#999;
        font-size: 12px;
        width:80px;
        height:30px;
        line-height:30px;
        text-align:center;
        border-right: 1px solid #ddd;
        cursor:pointer;
        &:hover{
          color:#fff;
          background:#494949;
        }
        &.active{
          background: #B02900;
          color:#fff;
        }
      }
    }
    .content {
      color:#fff;
    }
  }
</style>



