
<!--逐笔成交明细-->
<template>
  <div id='DeputeTransactionDetails'>
    <ul>
      <li class="nav">
        <span>成交时间</span>
        <span>价格</span>
        <span>手数</span>
        <span>成交手数</span>
        <span>类型</span>
        <span>主动</span>
        <span>被动</span>
      </li>
      <template v-for="a in result">
      <li class="text"  v-for="b in a.list">
          <span>{{a.stockTimeweidemo}}</span>
          <span>{{a.price}}</span>
          <span>{{b.m}}</span>
          <span>{{b.num}}</span>
          <span>{{b.type1}}</span>
          <span>{{b.type2 == false ? '':'特大'}}</span>
          <span>{{b.type3 == false ? '':'特大'}}</span>
      </li>
      </template>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'DeputeTransactionDetails',
    data () {
      return {

      }
    },
    components: {

    },
    props: {
      infoweituo: Array,
      result: Array
    }
  }
</script>

<style scoped lang='less'>
  *{
    font-size: 12px;
    color: #a7a2a2;
  }
  #DeputeTransactionDetails{
      display:flex;
      flex:auto;
    ul{
      border: 1px solid #ddd;
      border-top:none;
      flex-direction: column;
      padding-left: 3px;
      padding-right: 3px;
      display:flex;
      flex:auto;
        .nav{
          display: flex;
          justify-content: space-between;
          height: 30px;
          line-height: 30px;
        }
        .text{
          display: flex;
          justify-content: space-between;
          line-height: 200%;
          flex: auto;
          -webkit-box-flex: 1;
          -ms-flex: auto;
        }
    }
  }
</style>



